<template>
	<view class="container">
		<uni-notice-bar show-icon single text="点击右上角···收藏Team运动小程序,参赛更快捷" />
		<uni-swiper-dot class="uni-swiper-dot-box" @clickItem=clickItem :info="info" :current="current" mode="round"
			field="content">
			<swiper class="swiper-box" @change="changeBanner" :current="swiperDotIndex">
				<swiper-item v-for="(item, index) in info" :key="index">
					<view class="swiper-item" :class="item.colorClass">
						<text style="color: #fff; font-size: 32px;">{{index+1}}</text>
					</view>
				</swiper-item>
			</swiper>
		</uni-swiper-dot>
		<view class="nav">
			<uni-grid :show-border="false" :column="5" :highlight="true" @change="changeNav">
				<uni-grid-item v-for="(item, index) in navList" :index="index" :key="index">
					<view class="grid-item-box" style="background-color: #fff;">
						<uni-icons type="image" :size="30" color="#777" />
						<text class="text">{{item.text}}</text>
					</view>
				</uni-grid-item>
			</uni-grid>
		</view>

		<uni-section title='热门推荐' style='margin-top:10px'>
			<view class="list">
				<view class="list-item" v-for='(item,index) in hotProject' :key=index>

					<view class="list-pic">
						<uni-tag text="报名中" type="primary" class='item-tag' />
						<image :src="item.avatar" class="list-avatar"></image>
					</view>
					<view class="list-content">
						<view class="content-top">
							<view class="content-title">{{item.title}}</view>
						</view>
						<view class="content-bottom">
							<text class="chat-custom-text">
								<uni-icons type="location-filled" color="#999" size="17"></uni-icons>北京
							</text>
							<text class="chat-custom-text">2020-02-02 20:20</text>
						</view>
					</view>
				</view>
			</view>

		</uni-section>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				href: 'https://uniapp.dcloud.io/component/README?id=uniui',
				current: 0,
				swiperDotIndex: 0,
				modeIndex: -1,
				styleIndex: -1,
				info: [{
						colorClass: 'uni-bg-red',
						url: 'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-dc-site/094a9dc0-50c0-11eb-b680-7980c8a877b8.jpg',
						content: '内容 A',
					},
					{
						colorClass: 'uni-bg-green',
						url: 'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-dc-site/094a9dc0-50c0-11eb-b680-7980c8a877b8.jpg',
						content: '内容 B'
					},
					{
						colorClass: 'uni-bg-blue',
						url: 'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-dc-site/094a9dc0-50c0-11eb-b680-7980c8a877b8.jpg',
						content: '内容 C'
					}
				],
				navList: [{
						url: '/static/c1.png',
						text: '运动项目',
						badge: '0',
						type: "primary",
						link: '/pages/match/match',
						tabBar: true
						//'/pages/sportEvent/sportEvent'
					},
					{
						url: '/static/c2.png',
						text: '赛事报名',
						badge: '1',
						type: "success"
					},
					{
						url: '/static/c3.png',
						text: '平台活动',
						badge: '99',
						type: "warning",
						link: '/pages/mine/auth',
					},
					{
						url: '/static/c4.png',
						text: '通知公告',
						badge: '2',
						type: "error"
					},
					{
						url: '/static/c5.png',
						text: '体育商城'
					},
					{
						url: '/static/c6.png',
						text: '冠军榜'
					},
					{
						url: '/static/c7.png',
						text: '积分榜'
					},
					{
						url: '/static/c8.png',
						text: '勋章墙',
						badge: '8',
						type: "primary",
						link: '/pages/match/medalWall',
						tabBar: false
					},
					{
						url: '/static/c9.png',
						text: '等级榜'
					},
					{
						url: '/static/c9.png',
						text: '玩法攻略'
					},
				],
				hotProject: []
			}
		},
		created() {
			uni.request({
				url: 'home/hotActivity',
				success: res => {
					console.log(res)
				}
			})

			this.$http.request({
				url: 'home/hotActivity'
			}, 'get').then(res => {
				if (res.project) {
					this.hotProject = res.project;
				}
				console.log(111, res)
			})

		},
		methods: {
			changeBanner(e) {
				this.current = e.detail.current
			},
			changeNav(e) {
				let {
					index,
				} = e.detail
				console.log(8989, this.navList[index].link)
				// this.list[index].badge && this.list[index].badge++
				if (this.navList[index].tabBar) {
					uni.switchTab({
						url: this.navList[index].link
					})
				} else {
					uni.navigateTo({
						url: this.navList[index].link
					})
				}

			},

		}
	}
</script>

<style lang="scss" scoped>
	.container {
		// font-size: 14px;
		// line-height: 24px;
	}

	.swiper-item,
	.uni-swiper-dot-box {
		width: 100%;
		height: 100%;
		// border-radius: 10rpx;
	}

	.uni-bg-red {
		background-color: #ff5a5f;
	}

	.uni-bg-green {
		background-color: #09bb07;
	}

	.uni-bg-blue {
		background-color: #007aff;
	}

	.nav {
		width: 100%;
		box-sizing: border-box;
		background: #fff;
		font-size: 30rpx;
	}

	.grid-item-box {
		flex: 1;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		padding: 15px 0;
	}

	.grid-item-box-row {
		flex: 1;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
		padding: 15px 0;
	}

	// 列表
	.chat-custom-right {
		flex: 1;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: flex-end;
	}


	.list-item {
		width: 94%;
		margin: 0 auto;
		display: flex;
		padding: 20rpx 0;
		box-sizing: border-box;
		border-bottom: 1rpx solid #ccc;

		.list-pic {
			width: 260rpx;
			height: 200rpx;
			margin-right: 20rpx;
			position: relative;

			.item-tag {
				position: absolute;
				left: 0;
				top: 0;
				z-index: 10;
			}

			.list-avatar {
				width: 240rpx;
				height: 200rpx;
			}
		}

		.list-content {
			display: flex;
			flex-direction: column;
			justify-content: space-between;

			.content-bottom {
				display: flex;
				line-height: 40rpx;
				justify-content: space-between;
				align-items: center;

				.chat-custom-text {
					font-size: 28rpx;
					color: #999;
				}

			}
		}
	}
</style>
